.container {
  max-width: 1200px;
  width: 100%;
  margin: auto;
}

ul,
li {
  list-style-type: none;
  margin-bottom: 0;
}

body a {
  text-decoration: none;
  color: unset;
  transition: all 0.3s;
  cursor: pointer;
  font-family: $font-main;
  display: inline-block;
  color: $white;
  text-decoration: none;

  &:hover {
    color: $main-color;
    transition: all 0.3s;
  }
}

.center {
  text-align: center;
}

.color-main {
  color: $main-color;
}

.color-sub {
  color: $sub-color;
}
.color2 {
  color: #b9b9b9 !important;
}

.border-l {
  border-left-width: 1px !important;
}
.border-r {
  border-right-width: 1px !important;
}
.border-t {
  border-top-width: 1px !important;
}
.border-b {
  border-bottom-width: 1px !important;
}

.reverse {
  @include tablet {
    flex-direction: column-reverse;
  }
}

.d-m-none {
  @include tablet {
    display: none;
  }
}

.divider {
  background: rgba(255, 255, 255, 0.302);
  height: 1px;
  width: 100%;
  margin: 10px 0;
}
